import { Component, HostListener, OnInit } from '@angular/core';
import { PhotosService } from '../services/photos.service';
import { PhotoDataViewModel } from '../view-models/photo-data-viewmodel';

@Component({
  selector: 'app-photos',
  templateUrl: './photos.component.html',
  styleUrls: ['./photos.component.scss']
})
export class PhotosComponent implements OnInit {
  photos: PhotoDataViewModel[] | undefined;
  isFullscreen: boolean | undefined;

  constructor(private photosService: PhotosService) { }

  @HostListener('document:fullscreenchange', ['$event'])
  fullscreenmodes(event: Event): void {
    this.checkFullscreen();
  }

  ngOnInit(): void {
    this.getAllPhotos();
  }
  checkFullscreen(): void{
    this.isFullscreen = (document.fullscreenElement) ? true : false;
    console.log(this.isFullscreen)
  }

  getAllPhotos(): void {
    this.photosService.getPhotos().subscribe ((data) => {
      this.photos = data;
      document.getElementById('makeActive')?.classList.add('active');
    });
  }

  viewFullscreen(id: number): void {
    console.log(this.isFullscreen)
    if (this.isFullscreen === true) {
      this.exitFullscreen ();
    } else {
      const elem = document.getElementById('image_box_' + id);
      const methodToBeInvoked = elem?.requestFullscreen;
      if (methodToBeInvoked) { methodToBeInvoked.call(elem); }
    }
  }

  exitFullscreen(): void {
    console.log(this.isFullscreen)
    if (document.exitFullscreen) {
      document.exitFullscreen();
    }
  }

}
