import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl, SafeUrl } from '@angular/platform-browser';
import { MusicService } from '../services/music.service';
import { MusicDataViewModel } from '../view-models/music-data--viewmodel';

@Component({
  selector: 'app-music',
  templateUrl: './music.component.html',
  styleUrls: ['./music.component.scss']
})
export class MusicComponent implements OnInit {

  musicData: MusicDataViewModel[] | undefined;
  constructor(private musicService: MusicService, private santizer: DomSanitizer) { }

  ngOnInit(): void {
    this.musicService.getMusicData().subscribe ((data) => {
      this.musicData = data;
    });
  }
  getIFrameUrl(url?: string): SafeResourceUrl | undefined {
    if (url) {
      return this.santizer.bypassSecurityTrustResourceUrl (url);
    }
    return;
  }

}
