import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { ProjectDataViewModel } from '../view-models/project-data-viewmodel';
import { ProjectFeature } from '../view-models/project-feature';

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  constructor(private http: HttpClient) { }

  getProjects(): Observable<any>{
    return this.http.get('../assets/data/projects.json');
  }
  getProjectFeatures(rdnn: string): Observable<any> {
    return this.http.get('../assets/data/' + rdnn + '/' + rdnn + '.json');
  }
}
