import { Component, OnInit } from '@angular/core';
import { ProjectsService } from '../services/projects.service';
import { ProjectDataViewModel } from '../view-models/project-data-viewmodel';

@Component({
  selector: 'app-project-launcher',
  templateUrl: './project-launcher.component.html',
  styleUrls: ['./project-launcher.component.scss']
})
export class ProjectLauncherComponent implements OnInit {

  projectData: ProjectDataViewModel[] = [];

  constructor(private projectService: ProjectsService) {  }

  ngOnInit(): void {
    this.projectService.getProjects().subscribe ((data) => {
      console.log(data);
      this.projectData = data;
    });
  }

}
