import { Component, OnInit, Renderer2 } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Unsubscribable } from 'rxjs';
import { ProjectsService } from '../services/projects.service';
import { ProjectDataViewModel } from '../view-models/project-data-viewmodel';
import { ProjectFeature } from '../view-models/project-feature';

@Component({
  selector: 'app-project-landing',
  templateUrl: './project-landing.component.html',
  styleUrls: ['./project-landing.component.scss']
})
export class ProjectLandingComponent implements OnInit {

  id = -1;
  project: ProjectDataViewModel | undefined;
  rdnn: string | undefined;

  shrinkHeader = false;
  showDownload = false;

  // View Elements
  header: string | undefined;
  tagLine: string | undefined;
  description: string | undefined;
  animatedGif1: string | undefined;
  storeLinkText: string[] | undefined;
  storeLinkUri: string[] | undefined;
  storeBadgeUri: string[] | undefined;
  sourceCodeUri: string | undefined;
  sourceCodeName: string | undefined;
  helpUri: string | undefined;
  legalNotice: string | undefined;
  themeColor = '#7239b3';

  projectFeatures: ProjectFeature[] | undefined;


  // Scroll listener
  listener: any;
  constructor(private router: Router, private route: ActivatedRoute, private projectService: ProjectsService, private renderer2: Renderer2) {
    this.listener = this.renderer2.listen('window', 'scroll', (e) => {
      let pos = this.getYPosition(e);
      this.shrinkHeader = (pos !== undefined && pos > 4);
      this.showDownload = (pos !== undefined && pos > 700);
    });
  }
  getYPosition(e: Event): number | undefined {
    return (e.target as Document).scrollingElement?.scrollTop;
  }
  scrollToTop(): void{
    (function smoothscroll() {
        var currentScroll = document.documentElement.scrollTop || document.body.scrollTop;
        if (currentScroll > 0) {
            window.requestAnimationFrame(smoothscroll);
            window.scrollTo(0, currentScroll - (currentScroll / 8));
        }
    })();
  }

  scrollToDownload(event: Event): void{
    (function smoothscroll() {
        var currentScroll = document.getElementById('scroll-target');
        currentScroll?.scrollIntoView({ behavior: 'smooth' });
    })();
    event.stopPropagation();
  }

  ngOnInit(): void {
    this.route.params.subscribe((params) => {
      this.getProjectByName (params.name);
    });
  }
  getProjectByName(repo?: string): void {
    this.projectService.getProjects ().subscribe ((projects: ProjectDataViewModel[]) => {
      let i = 0;
      while (true) {
        if (projects[i] === undefined){
          break;
        }
        if (projects[i].repository === repo){
          this.id = i;
          break;
        }
        i++;
      }
      if (this.id === -1) {
        this.router.navigate(['']);
      }
      this.getProject();
    });
  }
  getProject(): void {
    this.projectService.getProjects ().subscribe ((projects) => {
      this.project = projects[this.id];
      this.rdnn = this.project?.rdnn;
      this.header = this.project?.name;
      this.tagLine = this.project?.tag_line;
      this.description = this.project?.description;
      this.animatedGif1 = this.project?.image;
      if (this.project?.theme_color) {
        this.themeColor = this.project?.theme_color;
      }

      this.storeLinkText = this.project?.store_link_text;
      this.storeLinkUri = this.project?.store_link_uri;
      this.storeBadgeUri = this.project?.store_badge_uri;

      this.sourceCodeName = this.project?.source_code_name;
      this.sourceCodeUri = this.project?.source_code_uri;
      this.helpUri = this.project?.help_uri;
      this.legalNotice = this.project?.other_legal_notice;

      this.getProjectFeatures();
    });
  }
  getProjectFeatures(): void {
    if (this.rdnn !== undefined) {
      this.projectService.getProjectFeatures(this.rdnn).subscribe ((features) => {
        this.projectFeatures = features;
      });
    }
  }

  isEven(n: number): boolean {
    return n % 2 === 0;
  }

}
